@import url(https://fonts.googleapis.com/css2?family=Karla:ital,wght@0,400;0,500;1,400;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body{font-family:'Karla', sans-serif}header{position:fixed;width:100%;z-index:2}nav{padding:1rem;left:0;top:0;position:fixed;width:100%;overflow:auto;background:white}nav ul{list-style:none;padding:0;margin:0;display:-webkit-flex;display:flex;-webkit-flex-flow:row nowrap;flex-flow:row nowrap;-webkit-justify-content:stretch;justify-content:stretch;width:calc(100% - 2rem)}nav li:not(:last-of-type){margin-right:.5rem}nav a{color:inherit;text-decoration:none}nav .navitem-container a{display:block}nav .navitem-container a.active{font-weight:800}.lang-toggle{display:-webkit-flex;display:flex;-webkit-flex-flow:column nowrap;flex-flow:column nowrap;border-left:1px solid black;padding-left:.5rem}.lang-toggle button:not(.is-active){cursor:pointer}.lang-toggle button.is-active{font-weight:800}footer{background-color:thistle}#wrapper{position:absolute;left:0;top:0;width:100%;height:100%;display:-webkit-flex;display:flex;-webkit-align-content:stretch;align-content:stretch}.HeaderNav{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;-webkit-flex-direction:row;flex-direction:row}#wrapper>main{display:-webkit-flex;display:flex;padding:1rem;padding-top:6rem;-webkit-justify-content:stretch;justify-content:stretch;-webkit-align-content:stretch;align-content:stretch}.ScrollyPage{position:relative}.ScrollyPage section{width:30%;margin-left:70%}.ScrollyPage aside{position:fixed;background:red;left:0;bottom:0;width:calc(70% - 3rem);height:calc(100vh - 7rem)}.Caller{background:lightgreen}.Caller.is-active{background:red;color:white}button{border:none;background:#dcdcdc;padding:.5rem;font-family:'Karla', sans-serif;outline:none;cursor:pointer}button.is-active{background:#323232;color:white}.SigmaComponent{border:1px solid lightgrey}.VisualizationController{width:100%;height:100%;position:absolute;box-sizing:content-box}.VisualizationController.networkGraph{padding-left:3rem}.loader-container{position:fixed;left:0;top:0;width:100%;height:100%;display:-webkit-flex;display:flex;-webkit-flex-flow:row nowrap;flex-flow:row nowrap;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.loader{background:lightgrey;height:10px;width:50%;position:relative}.loader .loading-bar{background:red;position:absolute;left:0;top:0;height:100%;transition:width .2s ease}

